export default {
  install (Vue) {
    const filter = {
      dateFormatFilter (date, fmt = 'yyyy-MM-dd hh:mm:ss') {
        if (isNaN(parseInt(date))) return '-';
        return Vue.prototype.$util.dateFormat(new Date(date), fmt);
      },
      // 内容为空
      nullFilter: (value, suffix = '') => {
        if (value === null || value === undefined || value === "" || (Array.isArray(value) || value.length === 0)) return '-'
        return value + suffix
      },

      assetStatusFilter: (value) => {
        if (value === true || value === 'true') {
          return '正常'
        } else if (value === false || value === 'false') {
          return '异常'
        } else {
          return value
        }
      }
    }
    
    for (let key in filter) {
      Vue.filter(key, filter[key])
    }

    Vue.filter('modalFilter', (value, args) => {
      if (!args) {
        return value
      }
      if (!Array.isArray(args)) {
        args = [args]
      }
      args.forEach(v => {
        let _filter = v.replace(/\(.*\)/, '')
        let _params = v.match(/\(.*\)/) && v.match(/\(.*\)/)[0]
        let _apply = [value]
        if (_params) {
          _params = _params.substr(1, _params.length - 2)
          _params = '[' + _params + ']'
          _params = JSON.parse(_params)
          _apply = _apply.concat(_params)
        }

        value = filter[_filter].apply(null, _apply)
      })
      return value
    })
    
    
    Vue.prototype.$tableFilter = filters => {
      let filter = Vue.filter('modalFilter')
      if (filters) {
        return (row, column, cellValue) => {
          return filter(cellValue, filters)
        }
      }
    }
  }
}


