const base = process.env.API_HOST;
export default (openFullScreen = () => {}, closeFullScreen = () => {}) => ({
  install (Vue, opt) {
    let instanceaxios = Vue.prototype.$http.create({
      headers: { "Content-Type": "application/json;charset=UTF-8" },
      withCredentials: true
    });

    
    // http response 服务器响应拦截器，这里拦截401错误，并重新跳入登页重新获取token
    instanceaxios.interceptors.response.use(
      response => {
        if ('success' in response.data) {
          return response;
        } else if ('result' in response.data) {
          response.data.success = !!response.data.result
        } else {
          response.data = {
            success: true,
            data: response.data
          }
        }
        return response;
      },
      error => {
        if (error.response) {
          switch (error.response.status) {
            case 999:
              localStorage.removeItem("user");
              location.hash = "/login";
              setTimeout(() => {
                location.reload()
              }, 0);
              return Promise.reject('Session 超时');
          }
        }
      }
    );


    // use custom message component
    const message = (msg, type, showClose) => {
      let msgs = {
        success: msg || "操作成功",
        error: msg || "操作失败"
      };
      Vue.prototype.$message({
        message: msgs[type],
        type: type,
        showClose: showClose
      });
    };

    // get
    const get = (url, param, successMsg, failMsg) => {
      openFullScreen();
      return instanceaxios
        .get(url, { params: param })
        .then(
          response => {
            closeFullScreen();
            let res = response.data;
            if (res.success) {
              successMsg && message(successMsg || res.info, "success", true);
              return Promise.resolve(res);
            } else {
              message(failMsg || res.info, "error", true);
            }
          },
          httpErr => {
            message("连接服务器失败", "error", true);
            return Promise.reject(httpErr);
          }
        )
        .catch(err => {
          closeFullScreen();
          return Promise.reject(err);
        });
    };

    // post
    const post = (url, param, {successMsg, failMsg} = {}) => {
      let failSilent = failMsg === false;
      let failOrigin = failMsg === true || failMsg === undefined;
      let successOrigin = successMsg === true;
      openFullScreen();
      return instanceaxios({
        method: "post",
        url: url,
        data: param
      })
        .then(
          response => {
            closeFullScreen();
            let res = response.data;
            if (res.success) {
              successMsg && message(successOrigin ? res.info : successMsg , "success", true);
              return Promise.resolve(res.data || res);
            } else {
              failSilent || message(failOrigin ? res.info : failMsg, "error", true);
              return Promise.reject(res.info);
            }
          },
          httpErr => {
            message("连接服务器失败", "error", true);
            return Promise.reject(httpErr);
          }
        )
        .catch(err => {
          closeFullScreen();
          return Promise.reject(err);
        });
    };
    const successMsg = "操作成功";
    const failMsg = "操作失败";

    const $api = {
      // 登陆
      login (param) {
        return post(`${base}user/login`, param, '登陆成功');
      },

      // 获取用户权限
      getUserFunctions (param) {
        return post(`${base}user/getFunctions`, param);
      },

      // 第三方接口-适配层文件更新结果
      programUpdate (param) {
        return post(`${base}program/update`, param)
      },

      // 适配层 - 适配层文件上传
      programUpload (param) {
        return post(`${base}program/upload`, param)
      },

      // 适配层 - 查询自增的业务以及机型
      programGetBusProList (param) {
        return post(`${base}program/getBusProList`, param)
      },

      // 适配层 - 查询业务列表
      programGetBusList (param) {
        return post(`${base}program/getBusList`, param)
      },

      // 适配层-自增 业务
      programSave (param) {
        return post(`${base}program/save`, param, '添加成功')
      },

      // 升级计划-查询升级计划详情
      autoGetDetail (param) {
        return post(`${base}auto/getDetail`, param)
      },

      // 升级计划-停止升级计划
      autoStopPlan (param) {
        return post(`${base}auto/stopPlan`, param, '停止成功')
      },

      // 升级计划-修改计划名称
      autoUpName (param) {
        return post(`${base}auto/upName`, param)
      },

      // 获取升级计划列表
      autoGetList (param) {
        return post(`${base}auto/getList`, param)
      },

      // 查询文件上传
      autoUpload (param) {
        return post(`${base}auto/upload`, param)
      },

      // 升级
      autoUpdate (param) {
        return post(`${base}auto/update`, param)
      },

      // 升级-确认返回总数
      autoUpCount (param) {
        return post(`${base}auto/upCount`, param)
      },
      
      programQueryProduct (param) {
        return post(`${base}program/queryProduct`, param)
      },

      // 根据用户ID取得用户权限
      userGetFunctions (param) {
        return post(`${base}user/getFunctions`, param)
      },

      // 导入SN列表（注册前导入）
      omcImport (param) {
        return post(`${base}omc/importSNList`, param)
      },

      // 查询获取局站管理列表
      omcQueryStationList (param) {
        return post(`${base}omc/queryStationList`, param)
      },

      // 导入查询表
      omcImportQueryStationFile (param) {
        return post(`${base}omc/importQueryStationFile`, param)
      },

      // 通过导入的查询文件，查询局站列表
      omcQueryStationsByImportFile (param) {
        return post(`${base}omc/queryStationsByImportFile`, param)
      },

      // 导出局站管理列表
      omcExportStationList (param) {
        return instanceaxios({
          method: 'post',
          url: `${base}omc/exportStationList`,
          data: param,
          responseType: 'arraybuffer'
        })
      },

      // 获取区域树
      getTree (param) {
        return post(`${base}address/getAll`, param)
      },











      // 新建CI类型及其关联关系
      CITypeAdd (param) {
        return post(`${base}CIType/add`, param, {successMsg: true})
      },

      // 查询CI类型及其关联的CI类型
      CITypeSearch (param) {
        return post(`${base}CIType/search`, param)
      },

      // 修改指定CI类型
      CITypeModify (param) {
        return post(`${base}CIType/modify`, param, {successMsg: true})
      },

      // 删除指定CI类型及其关联关系
      CITypeDelete (param) {
        return post(`${base}CIType/delete`, param, {successMsg: true})
      },

      // 新增CI连接类型
      CIConnectionTypeAdd (param) {
        return post(`${base}CIConnectionType/add`, param)
      },

      // 查询CI连接关系
      CIConnectionTypeSearch (param) {
        return post(`${base}CIConnectionType/search`, param)
      },

      // 新增指定CI类型之间的连接关系
      CITypeAddConnection (param) {
        return post(`${base}CIType/addConnection`, param)
      },

      // 删除指定CI类型之间的关联关系
      CITypeDeleteConnection (param) {
        return post(`${base}CIType/deleteConnection`, param)
      },

      // 查询指定CI类型之间的关联关系
      CITypeSearchConnection (param) {
        return post(`${base}CIType/searchConnection`, param)
      },

      // 新建CI属性及关联关系
      CIPropAdd (param) {
        return post(`${base}CIProp/add`, param)
      },

      // 查询CI类型及其关联的CI属性
      CIPropSearch (param) {
        return post(`${base}CIProp/search`, param)
      },

      // 修改指定CI类型的CI属性
      CIPropModify (param) {
        return post(`${base}CIProp/modify`, param)
      },

      // 删除指定CI类型的CI属性
      CIPropDelete (param) {
        return post(`${base}CIProp/delete`, param)
      },

      // 新建CI信息
      CIAdd (param) {
        return post(`${base}CI/add`, param)
      },

      // 新建指定CI信息的关联关系
      CIAddRelationship (param) {
        return post(`${base}CI/addRelationship`, param)
      },

      // 查询CI信息
      CISearch (param) {
        return post(`${base}CI/search`, param)
      },

      // 查询CI关联关系
      CISearchRelationship (param) {
        return post(`${base}CI/searchRelationship`, param)
      },
      
      // 修改指定CI信息
      CIModify (param) {
        return post(`${base}CI/modify`, param)
      },

      // 删除指定CI的关联关系
      CIDeleteRelationship (param) {
        return post(`${base}CI/deleteRelationship`, param)
      },

      // 删除CI信息及其关联关系
      CIDelete (param) {
        return post(`${base}CI/delete`, param, {successMsg: true})
      },






      // 获取企业和服务列表
      enterpriseLevelControllerGetUniqueServiceList (param) {
        return post(`${base}enterpriseLevelController/getUniqueServiceList`, param)
      },
      // 企业服务告警等级-添加
      enterpriseLevelControllerAdd (param) {
        return post(`${base}enterpriseLevelController/add`, param)
      },

      // 企业服务告警等级-删除
      enterpriseLevelControllerDelete (param) {
        return post(`${base}enterpriseLevelController/delete`, param)
      },

      // 企业服务告警等级-修改
      enterpriseLevelControllerUpdate (param) {
        return post(`${base}enterpriseLevelController/update`, param)
      },

      // 企业服务告警等级-分页列表
      enterpriseLevelControllerList (param) {
        return post(`${base}enterpriseLevelController/list`, param)
      },

      // 企业服务告警等级-修改状态
      enterpriseLevelControllerUpdateState (param) {
        return post(`${base}enterpriseLevelController/updateState`, param)
      },

      // 企业服务告警等级-设置为默认告警等级或取消设置
      enterpriseLevelControllerUpdateDefault (param) {
        return post(`${base}enterpriseLevelController/updateDefault`, param)
      },

      // 企业服务告警等级-获取企业信息和服务信息
      enterpriseLevelControllerGetUniqueServiceList (param) {
        return post(`${base}enterpriseLevelController/getUniqueServiceList`, param)
      },

      // 设备型号告警等级-添加
      deviceTypeLevelControllerAdd (param) {
        return post(`${base}deviceTypeLevelController/add`, param)
      },

      // 设备型号告警等级-删除
      deviceTypeLevelControllerDelete (param) {
        return post(`${base}deviceTypeLevelController/delete`, param)
      },

      // 设备型号告警等级-修改
      deviceTypeLevelControllerUpdate (param) {
        return post(`${base}deviceTypeLevelController/update`, param)
      },

      // 设备型号告警等级-列表
      deviceTypeLevelControllerList (param) {
        return post(`${base}deviceTypeLevelController/list`, param)
      },

      // 设备型号告警等级-获取设备型号信息
      deviceTypeLevelControllerGetDeviceTypeList (param) {
        return post(`${base}enterpriseLevelController/getDeviceTypeList`, param)
      },

      // 自定义告警等级-添加	
      alarmLevelControllerAdd (param) {
        return post(`${base}alarmLevelController/add`, param)
      },

      // 自定义告警等级-删除	
      alarmLevelControllerDelete (param) {
        return post(`${base}alarmLevelController/delete`, param)
      },

      // 自定义告警等级-修改	
      alarmLevelControllerUpdate (param) {
        return post(`${base}alarmLevelController/update`, param)
      },

      // 自定义告警等级-列表	
      alarmLevelControllerList (param) {
        return post(`${base}alarmLevelController/list`, param)
      },

      // 自定义告警等级-列表	
      enterpriseLevelControllerGetLastUse (param) {
        return post(`${base}enterpriseLevelController/getLastUse`, param)
      },

      // 自定义告警投递-添加	
      deliverControllerAdd (param) {
        return post(`${base}deliverController/add`, param)
      },

      // 自定义告警投递-删除	
      deliverControllerDelete (param) {
        return post(`${base}deliverController/delete`, param)
      },

      // 自定义告警投递-修改	
      deliverControllerUpdate (param) {
        return post(`${base}deliverController/update`, param)
      },

      // 自定义告警投递-列表	
      deliverControllerList (param) {
        return post(`${base}deliverController/list`, param)
      },

      // 自定义告警投递-禁用/启用	
      deliverControllerUpdateStatus (param) {
        return post(`${base}deliverController/updateStatus`, param)
      },

      // 自定义告警投递-获取使用规则的用户列表	
      deliverControllerGetUseList (param) {
        return post(`${base}deliverController/getUseList`, param)
      },

      // 自定义告警投递-给投递规则授权用户	
      deliverControllerAuthUser (param) {
        return post(`${base}deliverController/authUser`, param)
      },

      // 自定义告警投递-获取企业服务用户列表
      deliverControllerGetUserList (param) {
        return post(`${base}deliverController/getUserList`, param)
      },

      // 自定义告警投递-获取已经绑定了的用户列表
      deliverControllerGetUseList (param) {
        return post(`${base}deliverController/getUseList`, param)
      },
      

      // 自定义告警周期-添加 
      alarmCycleControllerAdd (param) {
        return post(`${base}alarmCycleController/add`, param)
      },

      // 自定义告警周期-删除 
      alarmCycleControllerDelete (param) {
        return post(`${base}alarmCycleController/delete`, param)
      },

      // 自定义告警周期-修改 
      alarmCycleControllerUpdate (param) {
        return post(`${base}alarmCycleController/update`, param)
      },

      // 自定义告警周期-列表 
      alarmCycleControllerList (param) {
        return post(`${base}alarmCycleController/list`, param)
      },

      // 自定义告警周期-更改状态
      alarmCycleControllerUpdateState (param) {
        return post(`${base}alarmCycleController/updateState`, param)
      },

      // 添加一个拓展信息
      alarmauxilaryControllerAdd (param) {
        return post(`${base}auxilaryController/add`, param)
      },

      // 获取拓展信息列表
      alarmauxilaryControllerList (param) {
        return post(`${base}auxilaryController/list`, param)
      },

      // 删除一个拓展信息
      alarmauxilaryControllerDelete (param) {
        return post(`${base}auxilaryController/delete`, param)
      },

      
    };

    Vue.prototype.$api || (Vue.prototype.$api = {});
    for (let i in $api) {
      // 云管在离开页面的时候会删除toDelete的api
      Vue.prototype.$api[i] || (Vue.prototype.$api[i] = $api[i]);
      $api[i].toDelete = true;
    }
  }
});





