<template>
  <section id="navPage">

    <div class="top-bar">

			<div class="system-name">
				<!-- <img class="logo" :src="!backMark ? apLogo : yiyiLogo"> -->
			</div>
<!--     
      <div class="menu first-menu" 
        v-for="menu in menus" :key="menu.name" 
				v-show = "!menu.hide"
        :class="isSelected(menu, currentMenu)"
        @click="selectMenu({level1:menu})">
        <a>{{menu.name}}</a>
      </div> -->


      <div class="exit">
        <a @click="logout();" class="ic ic-logout ic-2x" title="退出"></a>
      </div>

    </div>
  </section>

</template>

<script>
export default {
  name: 'Nav',
  data () {
    return {
      noticeCount: 0
    }
  },
  methods: {
    logout () {
			sessionStorage.removeItem('user');
			this.$router.push('/login')
		},

  }
}
</script>

<style lang="less" scoped>
@color-primary: #3AB0FF;
@color-bg-dark: #051530;
@color-font: #DDD;
@gap: 16px;

@top-height: 56px;

@first-menu-height: 60px;
@first-menu-width: 232px;
@second-menu-width: 120px;
@first-menu-color: #051530;
@triangle-width: 4px;

@first-menu-hover-color: #0c5ba0;
@first-menu-hover-bar-color: @color-primary;
@second-menu-color: #344261;
@second-menu-hover-color: @color-primary;
@info-bg: rgba(21, 37, 69, 0.9);

//====================================头部信息栏====================================
#navPage {

	.top-bar {
		z-index: 20;
		width: 100%;
		min-width: 800px;
		position: fixed;
		top: 0;
		left: 0;
		height: @top-height;
		background: @color-bg-dark;

		.logo {
			height: 56px;
			min-width: 28px;
			max-width: 320px;
			float: left;
			margin: 0px 32px 0px 22px;
		}

		> div {
			> h3{
				float: left;
				padding: 0 16px;
				font-weight: normal;
				font-size: 24px;
			}

			> h3, > a {
				color: @color-font;
				display: block;
				height: @top-height;
				line-height: @top-height;
				text-align: center;
				transition: all .3s;
			}

			&:hover > a{
				color: @color-primary;
			}
		}

		.first-menu{
			display: inline-block;
			font-size: 16px;
			padding: 0 12px;
			float: left;
			&.selected > a{
				color: @color-primary;
			}
		}

		.system-name {
			display: inline-block;
			height: @top-height;
			> h3 {
				padding-left: 8px;
				padding-right: 28px;
			}
		}
	}

	.system-name{
		float: left;
		> a {
			padding: 0 @gap;
		}
  }
  
	.notice,.info,.help,.exit{
		position: relative;
		float: right;
		width: 60px;
	}

	.info {
		.detail {
			position: absolute;
			width: 270px;
			max-height: 300px;
			right: 0;
			font-size: 14px;
			color: @color-font;
			background-color: @info-bg;
			display: none;

			.title {
				font-size: 20px;
				font-weight: bold;
				border-bottom: 1px solid rgba(255, 255, 255, 0.1);
				padding: 8px 16px;
			}

			table {
				width: 100%;
				margin: @gap 0;

				tr {
					height: 30px;
					> td {
						padding: 0 16px;
					}
					> td:nth-child(1) {
						text-align: left;
					}
					> td:nth-child(2) {
						text-align: right;
					}
					select{
						width: 150px;
						vertical-align: middle;
						margin-bottom: 0;
						height: 24px;
						line-height: 24px;
						padding-top: 0;
						padding-bottom: 0;
						text-align: right;
					}
					option{
						text-align: right;
					}
				}
			}

			span {
				word-break: break-all;
			}
		}

		&:hover {
			.detail {
				display: block;
			}
		}
	}

	.hot-point{
		position: absolute;
		left: 50%;
		top: 50%;
		margin-top: -15px;
		margin-left: 2px;

		min-width: 12px;
		height: 14px;
		line-height: 14px;
		border-radius: 7px !important;
		border: 2px solid @color-bg-dark;
		padding: 0 3px;
		font-size: 12px;

		background: #F00;
		color: #FFF;
		text-align: center;
  }
  .ic-2x{
    font-size: 28px;
  }


  .menu, .exit, .help, .info a, .notice, .exchange {
    cursor: pointer;
    a {
      text-decoration: none;
    }
  }
  .outset-shadow {
    box-shadow: 0 0 5px 0 #3AB0FF;
  }

}
</style>




