import Vue from 'vue'
import Router from 'vue-router'
import Main from '@/components/Main'

const Login = () => import(/* webpackChunkName: "Login" */  '../components/Login.vue');
const BatchUpdate = () => import(/* webpackChunkName: "BatchUpdate" */  '../components/BatchUpdate.vue');
const OMC = () => import(/* webpackChunkName: "OMC" */  '../components/OMC.vue');
const AlarmList = () => import(/* webpackChunkName: "AlarmList" */  '../components/alarmList.vue');
const AlarmLevel = () => import(/* webpackChunkName: "AlarmLevel" */  '../components/AlarmLevel.vue');
const AlarmCycle = () => import(/* webpackChunkName: "AlarmCycle" */  '../components/AlarmCycle.vue');
const AlarmPost = () => import(/* webpackChunkName: "AlarmPost" */  '../components/AlarmPost.vue');
const assetsManage = () => import(/* webpackChunkName: "assetsManage" */  '../components/assetsManage.vue');
const Topology = () => import(/* webpackChunkName: "Topology" */  '../components/topology.vue');
const ReportSetting = () => import(/* webpackChunkName: "ReportSetting" */  '../components/reportSetting.vue');
const CustomReport = () => import(/* webpackChunkName: "CustomReport" */  '../components/customReport.vue');

const HomeRepo = () => import(/* webpackChunkName: "HomeRepo" */  '../components/home_repo.vue');

Vue.use(Router)

