// let OMCRouter = (process.env.SYSTEM === 'OMC' || process.env.SYSTEM === 'all') ? [{
//   path: '/',
//   name: 'Main',
//   props: { auth: false },
//   component: Main,
//   beforeEnter (to, from, next) {
