export default new Router({
  routes: [
    {
      path: '/',
      name: 'Main',
      props: { auth: false },
      component: Main,
      children: [
        {
          path: '/alarmList',
          name: 'AlarmList',
          component: AlarmList
        },
        {
          path: '/alarmLevel',
          name: 'AlarmLevel',
          component: AlarmLevel
        },
        {
          path: '/AlarmCycle',
          name: 'AlarmCycle',
          component: AlarmCycle
        },
        {
          path: '/AlarmPost',
          name: 'AlarmPost',
          component: AlarmPost
        },
        {
          path: '/assetsManage',
          name: 'assetsManage',
          component: assetsManage
        },
        {
          path: '/topo',
          name: 'Topology',
          component: Topology
        },
        {
          path: '/ReportSetting',
          name: 'ReportSetting',
          component: ReportSetting
        },    
        {
          path: '/CustomReport',
          name: 'CustomReport',
          component: CustomReport
        },        
        {
          path: '/home_repo',
          name: 'HomeRepo',
          component: HomeRepo
        },
      ]
    },
    {
      // 空路由 返回之前的页面 
      path: '*',
      beforeEnter (to, from, next) {
        next(from.path)
      }
    }
  ]
})



