<template>
  <div id="main">
    <scloud-nav v-if="system==='upgrade'"></scloud-nav>
    <router-view :style="system==='OMC' && 'height:100%;margin-top:0;'" class='main-view'/>
  </div>
</template>

<script>
import scloudNav from './Nav.vue'
export default {
  name: 'Main',
  props: {auth: false},
  components: {
    "scloud-nav": scloudNav
  },
  data () {
    return {
      system: process.env.SYSTEM
    }
  },
  created () {
    // this.constructor.prototype.__proto__.$privilegeCode = [];
    // this.constructor.prototype.__proto__.$hasAuth = id => this.constructor.prototype.__proto__.$privilegeCode.indexOf(id + '') > -1
    // this.auth && this.$api.getUserFunctions({id: this.$currentUser.id}).then(res => {
    //   this.constructor.prototype.__proto__.$privilegeCode = res.data.map(v => v.id + '');
    // })
  }
}
</script>

<style scoped>
#main{
  height: 100%;
}
.main-view{
  margin-top: 56px;
  height: calc(100% - 56px);
}
</style>



